body {
    background-color: #dedede !important;
}

/*--------------------------------------------------------------->
<!--------------- General style for the form -------------------->
<!---------------------------------------------------------------*/
.generalContainer {
    border-radius: 76px;
    background: #ece4e4;
    box-shadow: inset -6px -6px 15px #666666, inset 6px 6px 15px #ffffff;
}
/*---------------------------------------------------------------------------*/

/*--------------------------------------------------------------->
<!--------------- Styles for the to-do and sunken card effect -------------------->
<!---------------------------------------------------------------*/
.addTodoPrimary {
    border-radius: 55px;
    background: #0d6efd;
    box-shadow: inset -5px -5px 10px #052c65, inset 5px 5px 10px #15b0ff;
}
.todoPrimarySelected {
    border-radius: 55px;
    background: #0d6efd;
    box-shadow: -5px -5px 10px #052c65, 5px 5px 10px #15b0ff;
    opacity: 0.4;
    text-decoration: line-through;
    text-decoration-color: purple;
}

.addTodoWarning {
    border-radius: 55px;
    background: #ffc107;
    box-shadow: inset -5px -5px 10px #664d03, inset 5px 5px 10px #ffff0b;
}
.todoWarningSelected {
    border-radius: 55px;
    background: #ffc107;
    box-shadow: -5px -5px 10px #664d03, 5px 5px 10px #ffff0b;
    opacity: 0.4;
    text-decoration: line-through;
    text-decoration-color: purple;
}

.addTodoInfo {
    border-radius: 55px;
    background: #0dcaf0;
    box-shadow: inset -5px -5px 10px #055160, inset 5px 5px 10px #15ffff;
}
.todoInfoSelected {
    border-radius: 55px;
    background: #0dcaf0;
    box-shadow: -5px -5px 10px #055160, 5px 5px 10px #15ffff;
    opacity: 0.4;
    text-decoration: line-through;
    text-decoration-color: purple;
}

.addTodoSuccess {
    border-radius: 55px;
    background: #198754;
    box-shadow: inset -5px -5px 10px #0a3622, inset 5px 5px 10px #28d886;
}
.todoSuccessSelected {
    border-radius: 55px;
    background: #198754;
    box-shadow: -5px -5px 10px #0a3622, 5px 5px 10px #28d886;
    opacity: 0.4;
    text-decoration: line-through;
    text-decoration-color: purple;
}

.addTodoDanger {
    border-radius: 55px;
    background: #dc3545;
    box-shadow: inset -5px -5px 10px #58151c, inset 5px 5px 10px #ff556e;
}
.todoDangerSelected {
    border-radius: 55px;
    background: #dc3545;
    box-shadow: -5px -5px 10px #58151c, 5px 5px 10px #ff556e;
    opacity: 0.4;
    text-decoration: line-through;
    text-decoration-color: purple;
}

/*---------------------------------------------------------------------------*/

/*----------------------------------------------------------------->
<!------- Choice of colors for to-do and push button effect ------->
<!----------------------------------------------------------------*/
.cardPrimary {
    border-radius: 50%;
    background: #0d6efd;
    box-shadow: inset -5px -5px 10px #052c65, inset 5px 5px 10px #15b0ff;
    color: transparent;
}
.cardPrimarySelected {
    border-radius: 50%;
    background: #0d6efd;
    box-shadow: inset 5px 5px 0px #0b5fda, inset -5px -5px 0px #0f7dff;
}

.cardWarning {
    border-radius: 50%;
    background: #ffc107;
    box-shadow: inset -5px -5px 10px #664d03, inset 5px 5px 10px #ffff0b;
    color: transparent;
}
.cardWarningSelected {
    border-radius: 50%;
    background: #ffc107;
    box-shadow: inset 5px 5px 0px #dba606, inset -5px -5px 0px #ffdc08;
}

.cardInfo {
    border-radius: 50%;
    background: #0dcaf0;
    box-shadow: inset -5px -5px 10px #055160, inset 5px 5px 10px #15ffff;
    color: transparent;
}
.cardInfoSelected {
    border-radius: 50%;
    background: #0dcaf0;
    box-shadow: inset 5px 5px 0px #0baece, inset -5px -5px 0px #0fe6ff;
}

.cardSuccess {
    border-radius: 50%;
    background: #198754;
    box-shadow: inset -5px -5px 10px #0a3622, inset 5px 5px 10px #28d886;
    color: transparent;
}
.cardSuccessSelected {
    border-radius: 50%;
    background: #198754;
    box-shadow: inset 5px 5px 0px #167448, inset -5px -5px 0px #1d9a60;
}

.cardDanger {
    border-radius: 50%;
    background: #dc3545;
    box-shadow: inset -5px -5px 10px #58151c, inset 5px 5px 10px #ff556e;
    color: transparent;
}
.cardDangerSelected {
    border-radius: 50%;
    background: #dc3545;
    box-shadow: inset 5px 5px 0px #bd2e3b, inset -5px -5px 0px #fb3c4f;
}
